*{
    margin:0;
    padding:0;
    box-sizing: border-box;
}
body {
    display: flex;
    flex-direction: column;
    padding: 0.5em 1em;
}

.cart {
    display: flex;
    flex-direction: column;
    gap: 3em;
    padding: 2em;
    background-color: #F2F2F2;

    .product {
        display: flex;
        gap: 1em;

        .item-img { //image
            max-width: 10em;
            border-radius: 12px;
        }
    }
    
    .item-info {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        flex-grow: 1; //thanks Kevin Powell

        .product-metadata {
            
            .name-price {
                display: flex;
                flex-direction: column;
                gap: 1em;
                
                .price {
                    display: flex;
                    gap: 1em;
                }
            }
        }

        .quantity {
            display: flex;
            gap: 1em;
            margin-bottom: 1em;
        }
    }
}

